@font-face {
  font-family: Avenir;
  font-weight: 300;
  src: url(./fonts/avenir/light/Avenir-Light.woff);
  src: url(./fonts/avenir/light/Avenir-Light.woff2);
}

@font-face {
  font-family: Avenir;
  font-weight: 400;
  src: url(./fonts/avenir/roman/Avenir-Roman.woff);
  src: url(./fonts/avenir/roman/Avenir-Roman.woff2);
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  min-width: 320px;
}

body, html {
  height: 100%; 
}

#root {
  height: 100%;
}

.page-wrapper {
  max-width: 800px;
  min-height: 100%;
  margin: 0 auto;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.main {
  flex: 1 0 auto;
  display: flex;
  width: 100%;
  padding: 60px 20px 77px;
  background: linear-gradient(45deg, #99cfc0 0%,#f2b1a2 89%); 
}

@import './components/Header/Header.scss';
@import './components/UserProfile/UserProfile.scss';
@import './components/Footer/Footer.scss';