.footer {
    display: flex;
    justify-content: space-between;
    align-items:center;
    padding: 8px 16px;

    @media (max-width:768px) {
        flex-wrap: wrap; 
    }

    .logo {
        width: 88px;
        margin: 0 15px;

        @media (max-width:768px) {
            margin-right: 0;
        }
    }

    .address {
        flex:1;
        font:normal 400 18px/1.2 Avenir, serif;
        color:#565656;                            

        @media (max-width:768px) {
            font-size: 14px;
        }
    }

    .nickname {
        flex:1;
        text-align: right;
        font:normal 400 18px/1.2 Avenir, serif;
        color:#565656;

        @media (max-width:768px) {
            flex:none;
            width: 100%;
            text-align: left;
            font-size: 14px;
        }
    }
}

